import React, { Component } from 'react'

export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="timeline">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">What is my experience?</span>
                <h2 className="colorlib-heading animate-box">PROFESSIONAL JOURNEY</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                    <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-4">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Graduation  <span>2019-present</span></h2>
                        <p>I am pursuing Masters of Computer Science from University Of Cincinnati.My Coursework inclides DataStructures and Algorithms, Cloud Computing,Intelligent Data Analysis, Database Theory. I am currently seeking for internship/full time opportunities whilst working on building my projects(web applications)using JavaScript. Also, I practice problem solving and algorithms using dataStructures to brush up java programming and critical thinking. </p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-1">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Web Application Developer at Accenture <span>2017-2019</span></h2>
                        <p>After my undergraduation, I had joined Accenture as an Associate Software Engineer. I have worked with two major clients as an automation expert cum web application developer.I was actively involved in development of financial applications right from requirement analysis till the production deployment pertaining to Agile flow of the busines.Fortunately, I have received "Star Of the Quarter" Award for the 2019 third quarter and was promoted to Software Engineer.</p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-2">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>IOT Engineer-Smart Bridge Solutions  <span>2016</span></h2>
                        <p>Designed and implemented a home automation based model using Internet of Things. The model is used by prototypical users who can monitor domestic/home appliances from anywhere over the world using a mobile or web server through Internet using Ethernet Shield. Worked on the Ethernet based module and developed a static mobile application for the users to operate the appliances.</p>
                      </div>
                    </div>
                  </article>
                  
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
